.about {
    width: 100%;
    margin-top: 50px;
    min-height: 100vh;
    margin: auto !important;

}

.about .about_menu {

    display: flex !important;
    justify-content: center;

    flex-wrap: wrap;
}


.about .right h1 {
    color: white;
    font-size: 3vmax !important;

}

