    margin-left: 200px;
    height: 100%;

}






@media(max-width:992px) {
    .about .about_menu {
        width: 100%;
        flex-wrap: wrap;
        display: grid;
        grid-template-columns: 1fr;
    }

    .about_button {
        width: 100%;
        margin-top: 20px !important;
    }

    .about .about_menu .right p {
        margin-bottom: 0px;
    }

    .about .about_menu .right {
        text-align: center !important;
        margin-left: 20px !important;
        margin-top: 135px;
    }

    .left video {

        max-width: 100% !important;
    }
}

.about_button {
    width: 200px;
    height: 70px;
    box-shadow: 0px 0px 5px gray;
    margin: auto;
    text-align: center;
    border: 2px solid white !important;
    background: none;
    color: white;
    font-size: 26px;
    transition: .5s;
    border-radius: 10px;
    border: none;

}

.aboutme {
    font-size: 3.2vmax !important;
}

@media(max-width:768px) {
    .about .about_menu .right {
        margin-top: 0px !important;
        margin-left: 0px !important;
    }
    .level .owj{
        border: none;
    }
    .btn_quetion {
        font-size: 16px !important;
    }

    .aboutme {
        font-size: 6vmax !important;
    }
}

.about_button:hover {
    background-color: white;
    color: black;
    cursor: pointer;
}

